body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    display: flex;
    flex-flow: row;
    align-items: center;
    padding: 20px 0 20px;
    font-family: 'Abel', sans-serif;
    font-size: 20px;
    position: fixed;
    top: 0;
    width: 100%;
    background: url(data:image/svg+xml;base64,alotofcodehere);
    background: -webkit-linear-gradient(bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%);
    background: -webkit-gradient(linear,  left bottom, left top,  from(rgba(255,255,255,0)),color-stop(15%, rgba(255,255,255,1)));
    background: -webkit-linear-gradient(bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 15%);
    background: linear-gradient(to top,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 15%);
}

.header h1 {
    font-weight: 100;
    margin-left: 25px;
}

.header img {
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    height: 2px;
}

.header img:hover {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
}

.normal-link {
    color: #5ae49f;
}

.normal-link:hover {
    text-decoration: underline;
}

.normal-link:visited {
    color: #5ae49f;
}

.current {
    font-size: 17px;
    color: #333333;   
}

.notCurrent {
    color: #7c7c7c;
    font-size: 15px;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
}

.notCurrent:hover {
    font-size: 17px;
    color: #5ae49f;
}

.header a img  {
    padding: 5px 40px 0 50px;
    height: 35px;
}

.info {
    display: flex;
    flex-flow: row;
    padding: 0 100px 0 0px;
    margin-top: 125px;
}

.list {
    display: flex;
    flex-flow: row;
    position: fixed;
}

.active li {
    background-color: #333333;
    color: white;
}

.active #link:hover {
    background-color: #333333;
    color: white;
    -webkit-transition-duration: 0s;
            transition-duration: 0s;
    font-size: 16px;
}

#link {
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
}
#link:hover {
    background-color: #f0f0f0;
    color: #333333;
    font-size: 16px;
}

.list ul {    
    list-style-type: none;
    padding: 0 20px;
    margin-top: 10px;
    position: -webkit-sticky;
    position: sticky;
}

.list ul li {
    width: 250px;
    height: 50px;
    text-align: center;
    padding: 0px 0 10px 0;
    text-decoration: none;
}

li{
    color: #333333;
}

.list Link{
    -webkit-text-decoration: white;
            text-decoration: white;
}

.list ul li h3 {
    margin-top: 10px;
    padding-top: 15px;
    font-size: 150%;
    font-family: 'Abel', sans-serif;
    font-weight: 150;
}

.vertical { 
    border-left: 1.5px solid rgb(82, 82, 82); 
    height: 65vh; 
    border-radius: 20px;
} 

.content {
    line-height: 1.7;
    margin-left: 350px;
    padding-bottom: 40px;
}

.content h1 {
    font-family: 'Abel', sans-serif;
    margin: 0 0 0 0;
    font-weight: 150;

}
.content p {
    margin-top: 10px;
}
.content h3 {
    font-family: 'PT Sans', sans-serif;
    color: #666666;
    padding: 0;
    margin: 0;
    font-weight: 500;
}

.content img {
    width: 700px;
    vertical-align: MIDDLE;
}
.tags {
    display: flex;
    flex-flow: wrap;
}
.tags h3 {
    margin: 5px 7px 5px 0;
    padding: 2px 10px;
    background-color: rgb(236, 236, 236);
    border-radius: 30px;
    font-size: 17px;
}

@media only screen and (max-width: 1080px) { 
    .list ul li {
        width: 190px;
        height: 50px;
        text-align: center;
        padding: 0px 0 10px 0;
        text-decoration: none;   
    }
    .content {
        margin-left: 270px;
    }
}

@media only screen and (max-width: 980px) { 
    .list  {
        visibility: hidden;
    }
    .content {
        margin-left: 40px;
    }
}
@media only screen and (max-width: 770px) { 
    .content img {
       width: 93%;
       margin: 20px 0;
    }
    .work {
        overflow-x: hidden;
    }
    .content {
        margin: 0;
        
    }
    .content p{
        margin: 0;
        width: 99%;
        font-size: 15px;
        -webkit-margin-before: 1em;
                margin-block-start: 1em;
        -webkit-margin-after: 1em;
                margin-block-end: 1em;
        -webkit-margin-start: 0px;
                margin-inline-start: 0px;
        -webkit-margin-end: 0px;
                margin-inline-end: 0px;
    }
    

    .header {
        padding-bottom: 30px;
    }

    .header a img {
        padding: 8px 5px 0 22px;
    }

    .header h1 {
        font-size: 25px;
        margin-left: 15px;
    }
    .info {
        padding: 0 10px 0 25px;
        margin-top: 100px;
        
    }
}

.Main {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    height: 95vh;
    padding: auto;
    font-family: 'Karla', sans-serif;
    color: #353636;
    padding-bottom: 40px;
    align-content: center;
}
.Personal {
    width: 50%;
}
.About {
    display: flex;
    flex-flow: row;
}
.About img {
    width: auto;
    height: 350px;
    margin: 30px 40px 30px 30px;
}
.About p {
    line-height: 22px;
    font-size: 15px;
}
.Info h1{
    margin-bottom: 3px; 
    /**font-family:  serif;*/
    font-weight: 100;  
    font-size: 35px;
    width: 370px;
    font-family: 'PT Sans', sans-serif;
    
}
.Info h4{
    margin: 0 0 15px 0;
    padding: 0;
    font-size: 15px;
    font-family: 'Abel', sans-serif;
    font-weight: 400;

}
#Edu {
    margin-bottom: 0px;
    font-weight: 510;
    font-size: 22px;
    font-family: 'PT Sans', sans-serif;

}
hr {
    border: 0.5px solid #444343;
    border-radius: 5px;
    margin: 20px 0;
}
.links {
    display: flex;
    flex-flow: row;
    justify-content: center;
     margin-top: 10px;
}
.Education h4 {
/**font-family:  serif;*/
    font-size: 20px;
    font-weight: 150px;
}
.Education p {
    font-size: 14.5px;
    margin-top: 5px;
}
.Link i {
    margin-right: 15px;
}
.Link {
    display: flex;
    flex-flow: row;
    margin: 20px 30px;
}
.Link {
    color: #353636;
    text-decoration: none;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;

}
.Link:hover {
    color: #5ae49f;
}

.Link p {
    margin: 8px 0;
}
.Experiences {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 20px;
}
.Work {
    width: 400px;
    height: 120px;
    margin: 0px 40px 28px;
    background-color: #333333;
    /**border-radius: 10px;
    
    -webkit-transition-duration: 0.4s; /* Safari */
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    padding: 4px;
    border: none;
}
.Work:hover {
    background-color: rgb(15, 15, 15);
    box-shadow: 0 0 white;
    -webkit-transform: translateX(3px);
            transform: translateX(3px);

}
.Work:active {
    background-color: rgb(0, 0, 0);
}


.Work:focus {
    outline: 0;
}
.Work h3{
    text-align: center;
    margin-top: 45px;
    color: white;
    font-size: 28px;
    font-weight: 50;
    text-decoration: none;
    font-family: 'PT Sans', sans-serif;
}

a {
    text-decoration: none;
}

@media only screen and (max-width: 1300px) {
    .Main {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 15%;
    margin-top: 20px;
    height: auto;
    margin-bottom: 110px;
    

    }
    .Personal {
        width: 80%;
        height: 70%;
    
    }
    .Experiences {
        align-items: center;
        margin-left: 0;
        width: 80%;
        
    }
    .links {
        position: relative;
        top: 500px;
        padding-bottom: 30px;
        height: 0px;
    }
}
@media only screen and (max-width: 940px) {
    .Personal {
        width: 90%;
    }
}
@media only screen and (max-width: 834px) {
    .About img {
        width: 0;
        margin: 0;
    }
    .Experiences {
        width: 90%;
    }
    .Main {
        margin-left: 10%;
    }
    .Info h1{
        
        width: auto;
    }
}
@media only screen and (max-width: 720px) {
    .links {
        display: flex;
        flex-flow: wrap;
        top: 500px;
        margin-left: 50px;
    }
    .Link {
        width: 30%;
    }
    .Main {
        margin-bottom: 190px;
    }
}
@media only screen and (max-width: 680px) {
    .Main {
        margin-left: 5%;
        
    }
    .Experiences {
        margin-left: 0;
        width: 95%;
    }
}
@media only screen and (max-width: 600px) {
    .Work {
        margin: 15px 35px;   
    }
    .Work h3{
        margin-top: 45px;
    }
    .Personal {
        margin-left: 10px;
    }
    .links {
        top: 525px;
        margin-left: 25px;
        padding: 0;
    }
}
@media only screen and (max-width: 550px) {
    .Work {
     width: 320px;
     height: 100px;
     margin: 15px 20px;
    }
    .Work h3{
        font-size: 25px;
        margin-top: 38px;
    }
    
    .links {
        top: 450px;
        margin-left: 25px;
    }
}
@media only screen and (max-width: 470px) {
    .links {
        top: 425px;
        margin-left: 10px;
    }
}

@media only screen and (max-width: 400px) {
    .Work {
        width: 270px;
        height: 80px;   
        margin: 10px 20px;
    }
    .Work h3 {
        margin-top: 25px;
    }
    .Link {
        margin-left: 13px;
    }
    .Info h4{
        font-size: 14px;
    }
    .Info h1 {
        font-size: 25px;
    }
    .links {
        top: 350px;
        margin-left: 5px;
    }
    
}

@media only screen and (max-width: 350px) {
    .Work {
        margin: 10px 5px;
        width: 260px;
        height: 80px;   
    }
    .Work h3{
        font-size: 24px;
    }
    .Info h1{
        font-size: 28px;
    }
    .Link {
        margin-left: 0px;
        width: 33%;
    }
}



