.Main {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    height: 95vh;
    padding: auto;
    font-family: 'Karla', sans-serif;
    color: #353636;
    padding-bottom: 40px;
    align-content: center;
}
.Personal {
    width: 50%;
}
.About {
    display: flex;
    flex-flow: row;
}
.About img {
    width: auto;
    height: 350px;
    margin: 30px 40px 30px 30px;
}
.About p {
    line-height: 22px;
    font-size: 15px;
}
.Info h1{
    margin-bottom: 3px; 
    /**font-family:  serif;*/
    font-weight: 100;  
    font-size: 35px;
    width: 370px;
    font-family: 'PT Sans', sans-serif;
    
}
.Info h4{
    margin: 0 0 15px 0;
    padding: 0;
    font-size: 15px;
    font-family: 'Abel', sans-serif;
    font-weight: 400;

}
#Edu {
    margin-bottom: 0px;
    font-weight: 510;
    font-size: 22px;
    font-family: 'PT Sans', sans-serif;

}
hr {
    border: 0.5px solid #444343;
    border-radius: 5px;
    margin: 20px 0;
}
.links {
    display: flex;
    flex-flow: row;
    justify-content: center;
     margin-top: 10px;
}
.Education h4 {
/**font-family:  serif;*/
    font-size: 20px;
    font-weight: 150px;
}
.Education p {
    font-size: 14.5px;
    margin-top: 5px;
}
.Link i {
    margin-right: 15px;
}
.Link {
    display: flex;
    flex-flow: row;
    margin: 20px 30px;
}
.Link {
    color: #353636;
    text-decoration: none;
    transition-duration: 0.2s;

}
.Link:hover {
    color: #5ae49f;
}

.Link p {
    margin: 8px 0;
}
.Experiences {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 20px;
}
.Work {
    width: 400px;
    height: 120px;
    margin: 0px 40px 28px;
    background-color: #333333;
    /**border-radius: 10px;
    
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    padding: 4px;
    border: none;
}
.Work:hover {
    background-color: rgb(15, 15, 15);
    box-shadow: 0 0 white;
    transform: translateX(3px);

}
.Work:active {
    background-color: rgb(0, 0, 0);
}


.Work:focus {
    outline: 0;
}
.Work h3{
    text-align: center;
    margin-top: 45px;
    color: white;
    font-size: 28px;
    font-weight: 50;
    text-decoration: none;
    font-family: 'PT Sans', sans-serif;
}

a {
    text-decoration: none;
}

@media only screen and (max-width: 1300px) {
    .Main {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 15%;
    margin-top: 20px;
    height: auto;
    margin-bottom: 110px;
    

    }
    .Personal {
        width: 80%;
        height: 70%;
    
    }
    .Experiences {
        align-items: center;
        margin-left: 0;
        width: 80%;
        
    }
    .links {
        position: relative;
        top: 500px;
        padding-bottom: 30px;
        height: 0px;
    }
}
@media only screen and (max-width: 940px) {
    .Personal {
        width: 90%;
    }
}
@media only screen and (max-width: 834px) {
    .About img {
        width: 0;
        margin: 0;
    }
    .Experiences {
        width: 90%;
    }
    .Main {
        margin-left: 10%;
    }
    .Info h1{
        
        width: auto;
    }
}
@media only screen and (max-width: 720px) {
    .links {
        display: flex;
        flex-flow: wrap;
        top: 500px;
        margin-left: 50px;
    }
    .Link {
        width: 30%;
    }
    .Main {
        margin-bottom: 190px;
    }
}
@media only screen and (max-width: 680px) {
    .Main {
        margin-left: 5%;
        
    }
    .Experiences {
        margin-left: 0;
        width: 95%;
    }
}
@media only screen and (max-width: 600px) {
    .Work {
        margin: 15px 35px;   
    }
    .Work h3{
        margin-top: 45px;
    }
    .Personal {
        margin-left: 10px;
    }
    .links {
        top: 525px;
        margin-left: 25px;
        padding: 0;
    }
}
@media only screen and (max-width: 550px) {
    .Work {
     width: 320px;
     height: 100px;
     margin: 15px 20px;
    }
    .Work h3{
        font-size: 25px;
        margin-top: 38px;
    }
    
    .links {
        top: 450px;
        margin-left: 25px;
    }
}
@media only screen and (max-width: 470px) {
    .links {
        top: 425px;
        margin-left: 10px;
    }
}

@media only screen and (max-width: 400px) {
    .Work {
        width: 270px;
        height: 80px;   
        margin: 10px 20px;
    }
    .Work h3 {
        margin-top: 25px;
    }
    .Link {
        margin-left: 13px;
    }
    .Info h4{
        font-size: 14px;
    }
    .Info h1 {
        font-size: 25px;
    }
    .links {
        top: 350px;
        margin-left: 5px;
    }
    
}

@media only screen and (max-width: 350px) {
    .Work {
        margin: 10px 5px;
        width: 260px;
        height: 80px;   
    }
    .Work h3{
        font-size: 24px;
    }
    .Info h1{
        font-size: 28px;
    }
    .Link {
        margin-left: 0px;
        width: 33%;
    }
}


