.header {
    display: flex;
    flex-flow: row;
    align-items: center;
    padding: 20px 0 20px;
    font-family: 'Abel', sans-serif;
    font-size: 20px;
    position: fixed;
    top: 0;
    width: 100%;
    background: url(data:image/svg+xml;base64,alotofcodehere);
    background: -moz-linear-gradient(bottom,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 30%);
    background: -webkit-linear-gradient(bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%);
    background: -o-linear-gradient(bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%);
    background: -ms-linear-gradient(bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%);
    background: linear-gradient(to top,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 15%);
}

.header h1 {
    font-weight: 100;
    margin-left: 25px;
}

.header img {
    transition-duration: 0.4s;
    height: 2px;
}

.header img:hover {
    transform: translateX(-5px);
}

.normal-link {
    color: #5ae49f;
}

.normal-link:hover {
    text-decoration: underline;
}

.normal-link:visited {
    color: #5ae49f;
}

.current {
    font-size: 17px;
    color: #333333;   
}

.notCurrent {
    color: #7c7c7c;
    font-size: 15px;
    transition-duration: 0.3s;
}

.notCurrent:hover {
    font-size: 17px;
    color: #5ae49f;
}

.header a img  {
    padding: 5px 40px 0 50px;
    height: 35px;
}

.info {
    display: flex;
    flex-flow: row;
    padding: 0 100px 0 0px;
    margin-top: 125px;
}

.list {
    display: flex;
    flex-flow: row;
    position: fixed;
}

.active li {
    background-color: #333333;
    color: white;
}

.active #link:hover {
    background-color: #333333;
    color: white;
    transition-duration: 0s;
    font-size: 16px;
}

#link {
    transition-duration: 0.3s;
}
#link:hover {
    background-color: #f0f0f0;
    color: #333333;
    font-size: 16px;
}

.list ul {    
    list-style-type: none;
    padding: 0 20px;
    margin-top: 10px;
    position: sticky;
}

.list ul li {
    width: 250px;
    height: 50px;
    text-align: center;
    padding: 0px 0 10px 0;
    text-decoration: none;
}

li{
    color: #333333;
}

.list Link{
    text-decoration: white;
}

.list ul li h3 {
    margin-top: 10px;
    padding-top: 15px;
    font-size: 150%;
    font-family: 'Abel', sans-serif;
    font-weight: 150;
}

.vertical { 
    border-left: 1.5px solid rgb(82, 82, 82); 
    height: 65vh; 
    border-radius: 20px;
} 

.content {
    line-height: 1.7;
    margin-left: 350px;
    padding-bottom: 40px;
}

.content h1 {
    font-family: 'Abel', sans-serif;
    margin: 0 0 0 0;
    font-weight: 150;

}
.content p {
    margin-top: 10px;
}
.content h3 {
    font-family: 'PT Sans', sans-serif;
    color: #666666;
    padding: 0;
    margin: 0;
    font-weight: 500;
}

.content img {
    width: 700px;
    vertical-align: MIDDLE;
}
.tags {
    display: flex;
    flex-flow: wrap;
}
.tags h3 {
    margin: 5px 7px 5px 0;
    padding: 2px 10px;
    background-color: rgb(236, 236, 236);
    border-radius: 30px;
    font-size: 17px;
}

@media only screen and (max-width: 1080px) { 
    .list ul li {
        width: 190px;
        height: 50px;
        text-align: center;
        padding: 0px 0 10px 0;
        text-decoration: none;   
    }
    .content {
        margin-left: 270px;
    }
}

@media only screen and (max-width: 980px) { 
    .list  {
        visibility: hidden;
    }
    .content {
        margin-left: 40px;
    }
}
@media only screen and (max-width: 770px) { 
    .content img {
       width: 93%;
       margin: 20px 0;
    }
    .work {
        overflow-x: hidden;
    }
    .content {
        margin: 0;
        
    }
    .content p{
        margin: 0;
        width: 99%;
        font-size: 15px;
        margin-block-start: 1em;
        margin-block-end: 1em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
    }
    

    .header {
        padding-bottom: 30px;
    }

    .header a img {
        padding: 8px 5px 0 22px;
    }

    .header h1 {
        font-size: 25px;
        margin-left: 15px;
    }
    .info {
        padding: 0 10px 0 25px;
        margin-top: 100px;
        
    }
}
